@media (min-width: 1400.1px) {
  .mealContainer {
    width: 100%;

    .imgContainer {
      width: 270px;
      margin: 5px auto;

      img {
        width: 100%;
        height: 200px;
        object-fit: cover;
      }
    }

    .item-details {
      p {
        font-size: 1.5rem;
        font-weight: bold;
      }

      .added-popup {
        position: absolute;
        border: none;
      }
    }
  }
}

@media (max-width: 1400px) {
  .mealContainer {
    .imgContainer {
      width: 270px;
      margin: 5px auto;

      img {
        width: 100%;
        height: 200px;
        object-fit: cover;
      }
    }

    .item-details {
      position: relative;
      p {
        font-size: 1.2rem;
        font-weight: bold;
      }

      .added-popup {
        position: absolute;
        right: 40%;
        bottom: 50px;
        padding: 5px 10px;
        font-size: 1rem;
        border-radius: 15px;
        background-color: black;
        color: white;
        border: none;
      }
    }
  }
}

